<template>
  <div
    class="card"
    :class="{
      // 被激活:点击图片显示标签
      card__active: card.active,
      // 被选择:点击标签显示详情页
      card__selected: selected
    }"
  >
    <card-front :card="card" @click.native="toggleCard({ card })" />
    <card-back :card="card" @click.native="handleClick" />
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import CardFront from './CardFront.vue'
import CardBack from './CardBack.vue'
export default {
  components: {
    CardFront,
    CardBack
  },
  props: {
    card: {
      type: Object
    },
    selected: {
      type: Boolean
    }
  },
  methods: {
    ...mapMutations(['toggleCard']),
    handleClick () {
      // 获取 #app 元素和当前元素的位置信息
      const appRect = document.querySelector('#app').getBoundingClientRect()
      const elRect = this.$el.getBoundingClientRect()
      // 获取当前卡片的信息
      const card = this.card
      // 创建一个矩形对象，存储位置和尺寸信息
      const rect = {}
      // 计算相对于 #app 元素的位置和尺寸
      rect.top = elRect.top - appRect.top
      rect.left = elRect.left - appRect.left
      rect.width = elRect.width
      rect.height = elRect.height
      // 存储 #app 元素的尺寸
      rect.appWidth = appRect.width
      rect.appHeight = appRect.height
      // 触发名为 'select' 的自定义事件，并传递矩形信息和卡片信息
      console.log('rect',rect)
      console.log('card',card)
      this.$emit('select', { rect, card })
    }
  }
}
</script>

<style lang="scss">
.card {
  position: relative;
  width: 100%;
  height: 100%;
}
.card__selected {
  visibility: hidden;
}
.card__active {
  .card_back {
    transform: scale(1);
    opacity: 1;
  }
  .card_front {
    // transition: transform 0.9s ease-in-out;
    transform: translate3d(0, -100px, 0);
  }
}
</style>
